<template>
    <div>
        <div class="sub-title m-30px-b">
            <h2 class="dark-color theme-after">{{ $t('createPattern') }}</h2>
        </div>
        {{ $t('defineColor') }}
        <div class="foo">
            <ul class="list-inline define-color-patterns">
                <li class="list-inline-item" v-for="(activeColor, alphabet) in defaultPattern" :key="alphabet">
                    <app-color-picker v-bind:alphabet="alphabet" v-bind:color="activeColor" v-on:change-alphabet-color="updateColorValue" :key="Math.floor(Math.random() * 1006)" />
                </li>
                <li class="save-pattern-button" v-show="iscolorChanged">
                    <a href="#" class="action-button theme-color" v-on:click.prevent="post">{{ $t('savePattern') }}</a>
                </li>
                <li class="save-pattern-button" v-show="defaultPatternDocKey != currentPatternKey">
                    <a href="#" class="action-button red-color" v-on:click.prevent="resetPattern">{{ $t('resetPattern') }}</a>
                </li>
            </ul>
            <div class="clearfix"></div>
        </div>                      
    </div>
</template>

<script>
    import colorPicker from "./ColorPicker.vue";
   const fb = require("@/firebase/Firebase");
    export default {
        components: {
            'app-color-picker': colorPicker
        },
        props: {
            defaultPatternDocKey: {
                required: true,
                type: String                
            },
            currentPatternKey: {
                required: true,
                type: String                
            },
            defaultPattern: {
                required: true,
                type: Object
            }
        },
        data: function() {
            return {
                iscolorChanged: false
            }
        },
        methods: {
            updateColorValue: function(val) {
                this.defaultPattern[val.alphabet] = val.color;
                this.$emit('change-current-color', val);
                this.iscolorChanged = true;
            },
            resetPattern: function() {
                this.$emit('reset-pattern', true);
            },
            post: function() {
                let _this = this;
                fb.db.collection('patterns').add({ pattern: this.defaultPattern })
                .then(function(docRef) {
                    _this.$emit('new-pattern-created', {'index': docRef.id, 'pattern': _this.defaultPattern});
                    _this.flashMessage.success({ message: 'Your color pattern was saved successfully.'});
                })
                .catch(function(error) {
                    _this.flashMessage.error({ message: 'Error saving your pattern. Please try again.'});
                    console.log("Error adding document: ", error);
                });
            }
        }
    }
</script>