<template>
    <div class="input-group color-picker" ref="colorpicker">
        <span class="input-group-addon color-picker-container">
            <span class="current-color" :style="'background-color: ' + colorValue" @click="togglePicker()"></span>
            <compact-color-picker :palette="palette" :value="colors" @input="updateFromPicker" v-if="displayPicker" />
        </span>
        <input type="text" maxlength="2" class="form-control" :style="{ color: colorValue }" v-bind:value="alphabet" @focus="showPicker()" @input="updateFromInput" />
    </div>
</template>

<script>
    import VueColour from "vue-color";
    var Compact = VueColour.Compact;
    export default {
        props: {
            'alphabet': {
                required: true,
                type: String
            },
            'color': {
                required: true,
                type: String
            }
        },
        components: {
            'compact-color-picker': Compact,
        },
        data() {
            return {
                colors: {
                    hex: this.color,
                },
                colorValue: '',
                palette: [
                    '#4D4D4D', '#999999', '#00FF00', '#F44E3B', '#FE9200', '#FCDC00',
                    '#DBDF00', '#A4DD00', '#68CCCA', '#73D8FF', '#AEA1FF', '#FDA1FF',
                    '#333333', '#808080', '#CCCCCC', '#D33115', '#E27300', '#FCC400',
                    '#B0BC00', '#68BC00', '#16A5A5', '#009CE0', '#7B64FF', '#FA28FF',
                    '#000000', '#666666', '#B3B3B3', '#9F0500', '#C45100', '#FB9E00',
                    '#808900', '#194D33', '#0C797D', '#0062B1', '#653294', '#AB149E',
                    '#84B84C'
                ],
                displayPicker: false,
            }
        },
        mounted() {
            this.setColor(this.color || '#696969');
        },
        methods: {
            setColor: function(color) {
                this.updateColors(color);
                this.colorValue = color;
            },
            updateColors: function(color) {
                if(color.slice(0, 1) == '#') {
                    this.colors = {
                        hex: color
                    };
                }
                else if(color.slice(0, 4) == 'rgba') {
                    var rgba = color.replace(/^rgba?\(|\s+|\)$/g,'').split(','),
                    hex = '#' + ((1 << 24) + (parseInt(rgba[0]) << 16) + (parseInt(rgba[1]) << 8) + parseInt(rgba[2])).toString(16).slice(1);
                    this.colors = {
                        hex: hex,
                        a: rgba[3],
                    }
                }
            },
            showPicker: function() {
                document.addEventListener('click', this.documentClick);
                this.displayPicker = true;
            },
            hidePicker: function() {
                document.removeEventListener('click', this.documentClick);
                this.displayPicker = false;
            },
            togglePicker: function() {
                this.displayPicker ? this.hidePicker() : this.showPicker();
            },
            updateFromInput: function() {
                this.updateColors(this.colorValue);
            },
            updateFromPicker: function(color) {
                this.colors = color;
                this.colorValue = (color.rgba.a == 1) ? color.hex : 'rgba(' + color.rgba.r + ', ' + color.rgba.g + ', ' + color.rgba.b + ', ' + color.rgba.a + ')';

                this.$emit('change-alphabet-color', {'alphabet': this.alphabet, 'color': this.colorValue });
            },
            documentClick: function(e) {
                let el = this.$refs.colorpicker;
                if(el != undefined && el !== e.target && !el.contains(e.target)) {
                    this.hidePicker()
                }
            }
        },
        watch: {
            colorValue: function(val) {
                if(val) {
                    this.updateColors(val);
                    this.$emit('input', val);
                }
            }
        }
    }
</script>