<template>
    <div>
        <ul>
            <li>
                <app-current-color-pattern v-bind:defaultPattern="defaultPattern"></app-current-color-pattern>
            </li>
        </ul>
        <h2 class="theme-after dark-color">{{ $t('userEnteredText') }}</h2>
        <ul>
            <li>
                <div class="r-info">
                    <form class="contactform" method="post">
                        <small><app-vue-typer v-bind:text="typerText" initial-action="erasing"></app-vue-typer></small>
                        <div class="form-group">
                            <textarea id="text" name="text" class="form-control" v-model="userEnteredText">{{ poem }}</textarea>
                            <span class="input-focus-effect theme-bg"></span>
                        </div>
                        <div class="send">
                            <button class="btn btn-default sm-p-5px" v-bind:class="{ active: !showWordMode }" type="submit" @click.prevent="showWordMode = false">{{ $t('letterMode') }}</button>&nbsp;
                            <button class="btn btn-default sm-p-5px" v-bind:class="{ active: showWordMode }" type="submit" @click.prevent="showWordMode = true">{{ $t('wordMode') }}</button>
                        </div>
                    </form> 
                </div>
            </li>
            <li v-if="userEnteredText.length > 0">
                <div class="r-info">
                    <small><app-vue-typer v-bind:text="copyText" pre-erase-delay="delay"></app-vue-typer></small>
                    <hr/>
                    <div id="color-picker-printable">
                        <p class="pattern-mode-p" ref="usercoloredtext" v-if="!showWordMode" v-html="letterMode"></p>
                        <p class="pattern-mode-p" ref="usercoloredtext" v-if="showWordMode" v-html="wordMode"></p>
                    </div>
                    <hr />
                    <a href="#" class="action-button theme-color" v-print="'#color-picker-printable'">Print Text</a>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import Vue from "vue"
    import { VueTyper } from "vue-typer"
    import VuePrint from "vue-print-nb"
    import currentColorPattern from "./CurrentColorPattern.vue"

    Vue.use(VuePrint);

    export default {
        props: {
            defaultPattern: {
                required: true,
                type: Object
            }
        },
        components: {
            'app-current-color-pattern': currentColorPattern,
            'app-vue-typer': VueTyper
        },
        data: function() {
            return {
                delay: 8000,
                userEnteredText: '',
                showWordMode: false,
            }
        },
        methods: {
        },
        watch: {
            onPaste: function() {
                return this.letterMode;
            },
            onkeyUp: function() {
                return this.letterMode;
            }
        },
        computed: {
            typerText: function() {
                return this.$i18n.messages[this.$i18n.locale].typeText;
            },
            copyText: function() {
                return this.$i18n.messages[this.$i18n.locale].copyText;
            },
            poem: function(lang) {
                lang = lang||'en';
                if(this.$i18n.locale === 'en' || lang === 'en')
                {
                    this.userEnteredText = `[EXAMPLE] - Vowels, a poem by Arthur Rimbaud

A black, E white, I red, U green, O blue: vowels, 
I shall tell, one day, of your mysterious origins: 
A, black velvety jacket of brilliant flies 
which buzz around cruel smells,

Gulfs of shadow; E, whiteness of vapours and of tents, 
lances of proud glaciers, white kings, shivers of cow-parsley; 
I, purples, spat blood, smile of beautiful lips 
in anger or in the raptures of penitence;

U, waves, divine shudderings of viridian seas, 
the peace of pastures dotted with animals, the peace of the furrows 
which alchemy prints on broad studious foreheads;

O, sublime Trumpet full of strange piercing sounds, 
silences crossed by Worlds and by Angels: 
O the Omega! the violet ray of His Eyes!

Vowels, A. Rimbaud`;
                }
                else 
                {
                    this.userEnteredText = `[EXEMPLE] - Voyelles, un pome d'Arthur Rimbaud

A noir, E blanc, I rouge, U vert, O bleu : voyelles,
Je dirai quelque jour vos naissances latentes :
A, noir corset velu des mouches clatantes
Qui bombinent autour des puanteurs cruelles,

Golfes dombre ; E, candeurs des vapeurs et des tentes,
Lances des glaciers fiers, rois blancs, frissons dombelles ;
I, pourpres, sang crach, rire des lvres belles
Dans la colre ou les ivresses pnitentes ;

U, cycles, vibrements divins des mers virides,
Paix des ptis sems danimaux, paix des rides
Que lalchimie imprime aux grands fronts studieux ;

O, suprme Clairon plein des strideurs tranges,
Silences traverss des Mondes et des Anges :
 O lOmga, rayon violet de Ses Yeux !

Arthur Rimbaud, Posies`;
                }
            }, 
            letterMode: function() {
                let str = '', _this = this;
                if( this.userEnteredText.length > 0)
                {
                    let words = this.userEnteredText.replace(/\r?\n/g, " <br/> ").split(" ");
                    words.forEach(function(word) {
                        if(word == '<br/>')
                        {
                            str += word;
                        }
                        else 
                        {
                            let _word = word.normalize('NFD').replace(/[\u0300-\u036f]/g, "");
                            for(let i= 0; i < word.length; i++)
                            {
                                let style = '';
                                if(_this.defaultPattern.hasOwnProperty(_word.charAt(i).toUpperCase()))
                                {
                                    style = "style='color:"+ _this.defaultPattern[_word.charAt(i).toUpperCase()] +";'";
                                }
                                str += '<span '+ style +' >'+ word.charAt(i) +'</span>';
                            }
                            str += ' ';
                        }
                    });
                }
                return str.trim();
            },
            wordMode: function() {
                let str = '', _this = this;
                if(this.userEnteredText.length > 0)
                {
                    let words = this.userEnteredText.replace(/\r?\n/g, " <br/> ").split(" ");
                    words.forEach(function(word) {
                        if(word == '<br/>')
                        {
                            str += word;
                        }
                        else
                        {
                            let _word = word.normalize('NFD').replace(/[\u0300-\u036f]/g, "");
                            str += '<span style="color:' + _this.defaultPattern[_word.charAt(0).toUpperCase()] +'">' + word + '</span> ';
                        }
                    });
                }
                return str;
            }
        }
    }
</script>
