<template>
	<div class="page resume-section white-bg p-65px-t">
		<div class="container-fluid p-0">
			<div class="row no-gutters">
				<div class="col-lg-3 col-md-3 col-xs-4 p-40px-t sm-m-50px-t">
					<div class="p-20px-l">
						<app-define-color-pattern v-bind:currentPatternKey='currentPatternKey' v-bind:defaultPatternDocKey='defaultPatternDocKey' v-bind:defaultPattern='defaultPattern' v-on:change-alphabet-color="updateDefaultColorValue" v-on:new-pattern-created="updateDefaultPattern" v-on:reset-pattern="resetPattern"></app-define-color-pattern>

						<div class="m-30px-b sm-m-20px-b">
							<div class="sub-title m-30px-b">
								<h2 class="dark-color theme-after">{{ $t('savedPatterns') }}</h2>
							</div>
							<div class="row">
								<div class="col-md-12">
									<div id="app-saved-patterns">
										<app-saved-color-patterns  v-bind:defaultPattern='defaultPattern' v-on:change-default-pattern="updateDefaultPattern"></app-saved-color-patterns>
									</div>
								</div>
							</div>
						</div>
					</div>  
				</div>
				<div class="col-lg-9 col-md-9 col-xs-10">
					<div class="page-scroll">
						<div class="page-content">
							<div class="row">
								<div class="col-md-12">
									<div class="resume-row">
										<h2 class="theme-after dark-color">{{ $t('currentPattern') }}</h2>
										<app-user-text v-bind:defaultPattern='defaultPattern'></app-user-text>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import defineColorPattern from '@/components/DefineColorPattern.vue';
	import savedColorPatterns from '@/components/SavedColorPatterns.vue';
	import userText from '@/components/UserText.vue';
	const fb = require("@/firebase/Firebase");

	export default {
		components: {
			'app-define-color-pattern': defineColorPattern,
			'app-saved-color-patterns': savedColorPatterns,
			'app-user-text': userText
		},
		data: function() {
			return {
                defaultPatternDocKey: 'fiRlYevZLQKqXiFZ4V1F',
				defaultPattern: {'A':'#696969','B':'#696969','C':'#696969','D':'#696969','E':'#696969','F':'#696969','G':'#696969','H':'#696969','I':'#696969','J':'#696969','K':'#696969','L':'#696969','M':'#696969','N':'#696969','O':'#696969','P':'#696969','Q':'#696969','R':'#696969','S':'#696969','T':'#696969','U':'#696969','V':'#696969','W':'#696969','X':'#696969','Y':'#696969','Z':'#696969'},
                currentPatternKey: '',
			}
		},
		created: function() {
            if(this.$storage.has('colorsyn-current-pattern'))
            {
                let cachedPattern = this.$storage.get('colorsyn-current-pattern');
                this.defaultPattern = cachedPattern.pattern;
                this.currentPatternKey = cachedPattern.index;
            }
		},
        methods: {
            updateDefaultColorValue: function(val) {
                this.defaultPattern[val.alphabet] = val.color;
            },
            updateDefaultPattern: function(pattern) {
            	this.defaultPattern = pattern.pattern;
                this.currentPatternKey = pattern.index;
            	this.$storage.set('colorsyn-current-pattern', pattern);
            	this.flashMessage.success({ message: 'New color pattern selected!'});
            },
            resetPattern: function() {
            	this.defaultPattern = {'A':'#696969','B':'#696969','C':'#696969','D':'#696969','E':'#696969','F':'#696969','G':'#696969','H':'#696969','I':'#696969','J':'#696969','K':'#696969','L':'#696969','M':'#696969','N':'#696969','O':'#696969','P':'#696969','Q':'#696969','R':'#696969','S':'#696969','T':'#696969','U':'#696969','V':'#696969','W':'#696969','X':'#696969','Y':'#696969','Z':'#696969'};
            	this.currentPatternKey = this.defaultPatternDocKey;

            	this.$storage.set('colorsyn-current-pattern', {'index': this.defaultPatternDocKey, 'pattern': this.defaultPattern });
            	this.flashMessage.success({ message: 'Color pattern reset to default!'});
            }
        }
	};
</script>