<template>
    <div class="r-info">
      <p class="current-pattern-p" v-html="currentPattern"></p>
    </div>
</template>

<script>
export default {
	props: {
		defaultPattern: {
			required: true,
			type: Object
		}
	},
	data () {
		return {
		}
	},
	computed: {
	    currentPattern: function() {
	        let str = ''
	        let _pattern = this.defaultPattern;
	        Object.keys(_pattern).forEach(function(alpa) {
	        	str += '<span style="color:' + _pattern[alpa] + ';">' + alpa + alpa.toLowerCase() +'</span> '
	        })
	        return str;
	    }
	}	
}
</script>
