<template>
    <ol class="saved-patterns m-0px p-0px p-20px-l">
        <li class="loading-list" v-if="isSavedPatternsloading">
            <div class="spinner-grow text-muted"></div>
            <div class="spinner-grow text-primary"></div>
            <div class="spinner-grow text-success"></div>
            <div class="spinner-grow text-info"></div>
            <div class="spinner-grow text-warning"></div>
            <div class="spinner-grow text-danger"></div>
            <div class="spinner-grow text-secondary"></div>
            <div class="spinner-grow text-dark"></div>
            <div class="spinner-grow text-light"></div>
        </li>
        <li class="p-5px-b p-5px-lr" v-for="(pattern, key) in savedPatterns" v-on:click="selectDefaultPattern(key)" :class="{ 'active' : activePattern == key }" :key="key">
            <span v-for="(activeColor, alphabet) in pattern" v-bind:style="{ color: activeColor }" :key="generateKey(key, alphabet)">{{alphabet}}{{ alphabet.toLowerCase() }} </span>
        </li>
    </ol>
</template>

<script>
    const fb = require("@/firebase/Firebase");
    export default {
        props: {
            defaultPattern: {
                required: true,
                type: Object
            }
        },
        data: function() {
            return {
                savedPatterns : {},
                activePattern: '',
                isSavedPatternsloading: true
            }
        },
        created: function() {
            if(this.$storage.has('colorsyn-current-pattern'))
            {
                let cachedPattern = this.$storage.get('colorsyn-current-pattern');
                this.activePattern = cachedPattern.index;
                this.savedPatterns[cachedPattern.index] =  cachedPattern.pattern;
            }

            let _this = this;
            fb.db.collection('patterns').get().then(function(row) {
                _this.isSavedPatternsloading = false;
                row.forEach(function(doc) {
                    if(doc.id != _this.activePattern)
                        _this.savedPatterns[doc.id] = doc.data().pattern;
                });
                _this.flashMessage.info({ message: 'Saved color patterns loaded!'});
            });
        },        
        methods: {
            generateKey: function(key, alphabet) {
                return key + alphabet;
            },
            selectDefaultPattern: function(index) {
                this.activePattern = index;
                let selectedPattern = this.savedPatterns[index];
                this.$delete(this.savedPatterns, index);
                let patterns = {};
                patterns[index] = selectedPattern;
                for( let i in this.savedPatterns)
                {
                    patterns [i] = this.savedPatterns[i];
                }
                this.savedPatterns = patterns;
                this.$emit('change-default-pattern', {'index': index, 'pattern': selectedPattern });
                this.flashMessage.success({ message: 'New color pattern selected.'});
            }
        },
        computed: {
            currentPattern: function() {
                let str = '';
                for(let k in this.defaultPattern)
                {
                    str += '<span style="color:' + this.defaultPattern[k] + ';">' + k + k.toLowerCase() +'</span> ';
                }
                return str;
            }
        }  
    }
</script>